___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Kanit", sans-serif;
}
body{
    background-color: #f8f8f8;
}
.container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  top: 20vh;
}
.input-div{
    width: 400px;
    height: 40px;
    border-radius: 20px 20px 0px 0px;
    border: none;
    background-color:#f0f0f0;
    text-align: center;
}
input{
    width: 90%;
    height: 90%;
    border: none;
    background-color: #f0f0f0;
    text-align: center;
    font-size: 1.2rem;
   
    margin-top: 5px;
}
input:focus{
    outline: none;
}
.cont {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  position: relative;
 
  width: 400px;
  height: 700px;
 
  border-radius:  0px 0px 20px 20px;
  color: #fff;
  padding: 10px;
}
.cont.day{
     background-color: #73beff;
       transition: background-color 1.5s ease;
    opacity: 1;
}
.cont.night{
    background-color:#5e5f61;
    opacity: 0.8; 
    transition: background-color 1.5s ease;
}
.header {
  display: flex;
  justify-content: space-between;
  margin: 15px;
  align-items: center;
}
.header > p {
  font-weight: 600;
}
.header > *{
    font-size: 1.2rem;
}

.sec1{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
    bottom: 100px;
    
}
.icon{
   font-size: 8rem;
  margin-left: 5px;
   height: 160px;
}
.temperature{
    font-size: 4rem;
    margin-left: 25px;
    height: 80px;
}

.location{
    font-size: 2rem;
}
.sec2{
    display: flex;
    justify-content: space-between;
    margin: 0 5px;
    
}
.sec2 >*{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.8rem;
    letter-spacing: 0.5px;
}
.sec2 >div>p{
font-size: 0.8rem;
}
i{
    cursor: pointer;
}
.toggle{
    cursor: pointer;
}
.toggle:hover{
   color: #c4c3c3;
   transition: .5s ease;
